@import url("https://fonts.googleapis.com/css2?family=Pacifico&display=swap");

.main_box {
  background-color: #ffffff;
  padding: 35px;
  border: 5px solid #e9e2f4;
  border-radius: 20px;
}
.small_box {
  background-image: linear-gradient(140deg, #c079ea, #6740b3);
  opacity: 0.8;
  padding: 25px;
  border-radius: 14px;
  margin-top: 10px;
}

.title {
  display: flex;
  align-items: center;
  text-indent: 15px;
}

h2 {
  margin-top: 0;
  color: #491d8f;
  font-family: Pacifico;
  /* text-align: center; */
  font-size: 30px;
  text-align: center;
}

img {
  width: 50px;
  margin-left: 10px;
  margin-bottom: 20px;
}

select,
input {
  padding: 7px 5px;
  border: none;
  border-radius: 4px;
  outline: none;
  color: #491d8f;
}

input {
  padding: 7px 5px;
  width: 130px;
  text-align: end;
  font-size: 16px;
}
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.label_box label {
  text-align: center;
  font-weight: 700;
  margin-left: 7px;
  font-size: 18px;
}

span {
  margin: 0 10px;
  font-size: 24px;
  font-weight: 900;
  color: #491d8f;
}

button {
  background-color: white;
  color: #491d8f;
  padding: 10px 15px;
  border: none;
  border-radius: 22px;
  font-weight: 900;
  cursor: pointer;
  transition: all 0.2s linear;
  margin-right: 10px;
}
button:hover {
  box-shadow: 0 0 4px 4px #b795ec;
}
